`, "",{"version":3,"sources":["webpack://./src/ui/App.vue"],"names":[],"mappings":";AAkLA;EACA,gBAAA;AACA","sourcesContent":["<template>\n  <v-app\n    id=\"app\"\n    :style=\"appStyle\">\n    <v-banner\n      v-if=\"isBrowser\"\n      color=\"primary\"\n      class=\"mb-1 mt-3 white--text\"\n      single-line>\n      {{ t('DescriptionDonateintervention') }}\n      <template #actions>\n        <v-btn\n          small\n          target=\"_blank\"\n          href=\"https://floccus.org/donate/\">\n          {{ t('LabelDonate') }}\n        </v-btn>\n      </template>\n    </v-banner>\n    <v-content>\n      <router-view />\n    </v-content>\n    <v-footer\n      color=\"blue darken-1\"\n      app>\n      <v-row no-gutters>\n        <v-col>\n          <v-btn\n            text\n            x-small\n            href=\"https://floccus.org\"\n            target=\"_blank\"\n            class=\"white--text\">\n            floccus v{{ VERSION }}\n          </v-btn>\n          <v-tooltip top>\n            <template #activator=\"{ on, attrs }\">\n              <v-btn\n                x-small\n                text\n                class=\"white--text\"\n                v-bind=\"attrs\"\n                :to=\"{name: routes.DONATE}\"\n                target=\"_blank\"\n                v-on=\"on\">\n                <v-icon>mdi-heart-outline</v-icon>\n              </v-btn>\n            </template>\n            <span>{{ t('LabelFunddevelopment') }}</span>\n          </v-tooltip>\n          <v-tooltip top>\n            <template #activator=\"{ on, attrs }\">\n              <v-btn\n                x-small\n                text\n                class=\"white--text\"\n                v-bind=\"attrs\"\n                :to=\"{name: routes.TELEMETRY}\"\n                target=\"_blank\"\n                v-on=\"on\">\n                <v-icon>{{ telemetryEnabled ? 'mdi-bug-play-outline' : 'mdi-bug-pause-outline' }}</v-icon>\n              </v-btn>\n            </template>\n            <span>{{ t('LabelTelemetry') }}</span>\n          </v-tooltip>\n          <v-tooltip top>\n            <template #activator=\"{ on, attrs }\">\n              <v-btn\n                x-small\n                text\n                class=\"white--text\"\n                v-bind=\"attrs\"\n                target=\"_blank\"\n                @click=\"openInNewTab\"\n                v-on=\"on\">\n                <v-icon>mdi-open-in-new</v-icon>\n              </v-btn>\n            </template>\n            <span>{{ t('LabelOpeninnewtab') }}</span>\n          </v-tooltip>\n        </v-col>\n      </v-row>\n    </v-footer>\n    <v-dialog\n      v-model=\"locked\"\n      :max-width=\"600\"\n      persistent>\n      <v-card>\n        <v-card-title><v-icon>mdi-lock-outline</v-icon>{{ t('LabelUnlock') }}</v-card-title>\n        <v-card-text>\n          <v-alert\n            v-if=\"unlockError\"\n            outlined\n            dense\n            :icon=\"false\"\n            type=\"warning\">\n            {{ unlockError }}\n          </v-alert>\n          <v-text-field\n            v-model=\"key\"\n            :label=\"t('LabelKey')\"\n            type=\"password\"\n            @keyup.enter=\"onUnlock\" />\n          <div class=\"d-flex flex-row-reverse\">\n            <v-btn\n              class=\"primary\"\n              @click=\"onUnlock\">\n              {{ t('LabelUnlock') }}\n            </v-btn>\n          </div>\n        </v-card-text>\n      </v-card>\n    </v-dialog>\n  </v-app>\n</template>\n\n<script>\nimport { version as VERSION } from '../../package.json'\nimport { actions } from './store/definitions'\nimport { routes } from './router'\nimport Controller from '../lib/Controller'\nimport browser from '../lib/browser-api'\nexport default {\n  name: 'App',\n  data() {\n    return {\n      VERSION,\n      key: '',\n      unlockError: null,\n      telemetryEnabled: false,\n    }\n  },\n  computed: {\n    locked() {\n      return this.$store.state.locked\n    },\n    routes() {\n      return routes\n    },\n    appStyle() {\n      return {\n        background: this.$vuetify.theme.dark ? '#000' : '#e1f5fe'\n      }\n    }\n  },\n  async created() {\n    await Promise.all([\n      this.$store.dispatch(actions.LOAD_LOCKED),\n      this.$store.dispatch(actions.LOAD_ACCOUNTS)\n    ])\n    const controller = await Controller.getSingleton()\n    const unregister = controller.onStatusChange(() =>\n      this.$store.dispatch(actions.LOAD_ACCOUNTS)\n    )\n    window.addEventListener('beforeunload', unregister)\n    window.addEventListener('beforeunload', unregister)\n    window.addEventListener('unload', unregister)\n    window.addEventListener('close', unregister)\n    const {telemetryEnabled} = await browser.storage.local.get({'telemetryEnabled': false})\n    this.telemetryEnabled = telemetryEnabled\n  },\n  methods: {\n    async onUnlock() {\n      try {\n        await this.$store.dispatch(actions.UNLOCK, this.key)\n      } catch (e) {\n        this.unlockError = e.message\n        this.key = ''\n      }\n    },\n    openInNewTab() {\n      window.open(window.location.href, '_blank')\n    }\n  }\n}\n</script>\n\n<style>\nbody {\n  min-width: 420px;\n}\n</style>\n"],"sourceRoot":""}]);
